  componentDidMount() {
    //window.addEventListener('scroll', this.handleScroll);

    ///load data
    ////1.loading data from completed flights - weight, fee and num of packages

    this.loadCompletedData();

    var pending = 30;
    var inprocess = 3;
    var ongoing = 0;

  /////counting ongoing(1), process(3) and pending(30) orders - bases

  database.ref('beckbag').child('merchantorders').child('added').once("value")
  .then(function(snapshot) {
    var pendingval = snapshot.numChildren();
    pending = pendingval + pending;
    console.log(pending);
  });

  database.ref('beckbag').child('merchantorders').child('matched').once("value")
  .then(function(snapshot) {
    var inprocessval = snapshot.numChildren();
    inprocess = inprocessval + inprocess;
    console.log(pending);
  });

  //change to ongoing
  database.ref('beckbag').child('merchantorders').child('matched').once("value")
  .then(function(snapshot) {
    var ongoingval = snapshot.numChildren();
    ongoing = ongoing + ongoingval;
    console.log(ongoing);
  });

  var countinside = 0;



  var that = this;
  ///////// loading notifications

  database.ref('beckbag').child('merchants').child('4de0e39c-d263-427f-a98f-2076a034b6c5').child('notifications')
        .limitToLast(8)
        .orderByChild('timestamp')
        .once('value')
        .then((snapshot) => {
          var count = snapshot.numChildren();
          snapshot.forEach((chSnapshot) => {
            
            var notification = chSnapshot.val();
            console.log(notification);
            notifications[countinside].type = notification.type;
            notifications[countinside].text = notification.text;
            console.log(notifications[countinside].type);
            if(notifications[countinside].type == 'Ongoing')
              {
                notifications[countinside].link = 'ongoing';
              }
            if(notifications[countinside].type == 'Add')
              {
                notifications[countinside].link = 'pending';
                notifications[countinside].type = 'Package Added';
              }
            if(notifications[countinside].type == 'AcceptFee')
              {
                notifications[countinside].link = 'inprocess';
                notifications[countinside].type = 'Fee Accepted';
              }    
            if(notifications[countinside].type == 'OngIssue')
              {
                notifications[countinside].link = 'ongoing';
                notifications[countinside].type = 'Ongoing - Issue Reported';
              } 
            if(notifications[countinside].type == 'ComPayment')
              {
                notifications[countinside].link = 'completed';
                notifications[countinside].type = 'Completed - Payment Status Updated';
              }
            if(notifications[countinside].type == 'ComIssue')
              {
                notifications[countinside].link = 'completed';
                notifications[countinside].type = 'Completed - Issue Reported';
              }        
            notifications[countinside].show = true;
            countinside++;
            //notifications.push(notification);
            if( countinside === count )
            {
              console.log('yay '+count+' '+countinside);
              that.setState({
                addedNotification: true
            });
            }
            
    });
  });   



  this.setState({
    pendingorders: pending,
    ongoingorders: ongoing,
    inprocessorders: inprocess
  });

  };

  onDateChange(dates, dateStrings) {
  var fromdate = dateStrings[0];
  fromdate = fromdate.split("-");
  var fromtimestamp = new Date(fromdate).getTime();
  var todate = dateStrings[1];
  todate = todate.split("-");
  var totimestamp = new Date(todate).getTime();

  this.setState({
      added: false
    });
    var list = [];
    if( this.state.searching){
      list = this.state.airlinelist;
    }
    else
    {
      list = this.state.notmodifiedlist;
    }
    var searchlist = [];
    var length = list.length;
    var searchlistcount = 0;
    var i = 0;
    for(i; i<length; i++)
    { 
      console.log(list[i])
      var timestamp = list[i].timestamp;
      var fromdiff = timestamp - fromtimestamp;
      var todiff = totimestamp - timestamp;
      if (( fromdiff >= 0 ) && ( todiff >= 0 ))
      {
        console.log('entered');
        searchlist[searchlistcount] = list[i];
        searchlistcount++;
      }
    }
    if(i === length){
      this.setState({
      added: true,
      airlinelist: searchlist,
      datechange: true
    });
    }
    
  }  

